html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}
html.lesson-menu-opened .cover, body.lesson-menu-opened .cover {
  display: block;
}
html.lesson-menu-opened .menu-page-course, body.lesson-menu-opened .menu-page-course {
  transform: translateX(0);
}
