.footer__text {
  margin: 0;
  color: #6F6F75;
}

.footer__rigth {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(66.6666666667% - 30px);
  margin-left: calc(8.3333333333% + 15px);
}
@media screen and (max-width: 1100px) {
  .footer__rigth {
    width: calc(75% - 30px);
  }
}
@media screen and (max-width: 991px) {
  .footer__rigth {
    width: calc(100% - 30px);
  }
}
@media screen and (max-width: 1100px) {
  .footer__rigth {
    margin-left: calc(0% + 15px);
  }
}
.footer__rigth .socials {
  margin-top: 15px;
  margin-bottom: 36px;
}
@media only screen and (max-width: 767.98px) {
  .footer__rigth .socials {
    margin-bottom: 20px;
  }
}

.footer__info {
  display: flex;
  justify-content: space-between;
  margin-left: -15px;
  margin-right: -15px;
}

.footer__column {
  padding: 0 15px;
}
@media only screen and (max-width: 767.98px) {
  .footer__column:not(:last-child) {
    display: none;
  }
}

.footer__title {
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 20px;
  font-family: "MontserratBold", sans-serif;
}

.footer-menu__item {
  list-style-type: none;
  margin-bottom: 5px;
}

.footer-menu__link {
  color: #6F6F75;
  text-decoration: none;
  font-size: 16px;
  line-height: 140%;
}

.footer__phone {
  font-size: 20px;
  line-height: 24px;
  font-family: "MontserratBold", sans-serif;
  text-decoration: none;
  color: #2D2D2F;
}
.footer__phone + .footer__text {
  margin-bottom: 22px;
}
@media only screen and (max-width: 767.98px) {
  .footer__phone + .footer__text {
    margin-bottom: 0;
  }
}

.footer__links .footer__text {
  margin-bottom: 5px;
}
.footer__links a {
  text-decoration: none;
  color: #6F6F75;
}

/*--------------------END footer----------------------*/