$maxWidth: 1920;//   . 
$maxWidthContainer: 1170;//    
$containerPaddings: 30;//      
$maxContainerWidth: $maxWidthContainer + $containerPaddings;//    

// colors
$accent: #FFEF68;
$yellow-accent: #FFE400;
$text: #2D2D2F;
$gray-text: #6F6F75;
$gray-bg: #F8F8FA;
$red:  #FF886A;

// paths
	// use: background-image: url($icos + 'arrow-right.svg');
$img: '../img/';
$icos: '../img/icons/';
$svg: '../img/icons-svg/';


// media
$lg: 1199px;
$md: 991px;
$sm: 767.98px;
$xs: 576px;
$xss: 480px;

// Customization Bootstrap vars from "app/libs/bootstrap/scss/_variables.scss"
$grid-gutter-width: 30px;
$grid-columns: 12;
$grid-breakpoints: (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px);
$container-max-widths: (sm: 540px, md: 720px, lg: 960px, xl: 1170px);

$gutter: $grid-gutter-width;

$reg: "MontserratRegular", sans-serif;
$med: "MontserratMeduim", sans-serif;
$semi: "MontserratSemiBold", sans-serif;
$bold: "MontserratBold", sans-serif;
$black: "MontserratBlack", sans-serif;
$ibm-reg: "IBMPlexSans-Regular", sans-serif;
$ibm-med: "IBMPlexSans-Medium", sans-serif;


