/*--------------------history-page----------------------*/
.wrapper-simple {}
.wrapper-flex {
	// flex-wrap: initial;
}
.page-history {
	.learn-in{
		flex: 1 1 auto;
		@include col();
		margin-left: 300px;
		@include max-screen($md){
			margin-left: 15px;
		} 
	}
}
.page-history__top {
	@include col();
	@include size(12);
	display: flex;

	@include max-screen($xs){
		flex-wrap: wrap;
	} 
	.tag{
		@extend .tag-mini;  
	} 
}
.page-history__img{
	flex: 0 0 270px;
	margin-right: 30px;
	@include max-screen($sm){
		flex-basis: auto;
	}

	.user-avatar{
		@include max-screen($sm){
			width: 150px;
			height: 150px;
		}  
		@include max-screen($xs){
			width: 64px;
			height: 64px;
		}
	}
	
	

}
.page-history__card {}
.card-page-history {
	padding-top: 27px;
	flex: 1 1 auto;
	@include max-screen($xs){
		padding-top: 5px;
	}  
	h2{
		margin-bottom: 0px;
		font-family: $bold;
		line-height: calc(28/28);
		@include adaptive-value('font-size', 28, 15, 1);

	}
}
.card-page-history__header {
	@include max-screen($xs){
		width: 100%;
	}  
}

.card-page-history__role {
	color: $gray-text;
	margin-bottom: 5px;
}
.card-page-history__link {
	margin-bottom: 10px;
	a{
		display: flex;
		align-items: center;
		color: $text;
		text-decoration: none;
		.icon{
			margin-right: 5px;
			color: $text;
			font-size: 15px;
		}

	}
}
.card-page-history__courses{
	@include max-screen($xs){
		flex: 1 1 auto;
		border-top: 1px solid $gray-text;
		padding-top: 15px;
		margin-top: 6px;
		margin-left: -20px;
		margin-right: -20px;
		padding-left: 20px;
		padding-right: 20px;
	}  
}
.page-history__content {

}
.content-page-history {
	@include col();
	@include size(12);
	padding-left: 300px;
	margin-bottom: 60px;

	@include max-screen($md){
		padding-left: 0;
	}  
	h1{
		margin-top: 50px;
		margin-bottom: 20px;
	    font-size: 28px;
	    line-height: -webkit-calc(34/28);
	    line-height: calc(34/28);
	}
	h2{
		margin-top: 50px;
	}

	button{
		margin-top: 15px;
		display: inline-flex;
	  align-items: center;
	  justify-content: center;
	  height: 43px;
	  border: none;
	  color: $text;
	  text-decoration: none;
	  background-color: transparent;
	  padding: 0 30px;
	  border-radius: 5px;
	  font-family: $semi;
	  text-align: center;
	  position: relative;
	  outline: none;
	  transition: all .1s ease;
	  cursor: pointer;
		border: 2px solid  #2D2D2F;

		 &:hover{
		     border-color: #FFE400;
		  }
				
		  &:active{
		 	border-color: #DBDBDB;
		 	color: #DBDBDB;
	  	}
	}
	img{
		max-width: 100%;
	}
	.ba-slider img{
		max-width: none;
	}
	figure{
		display: inline-block;
		max-width: 100%;
		margin-left: 0;
		margin-right: 0;
		figcaption{
			// margin-bottom: 30px;
			text-align: center;
		}
	}
}
.content-page__caption{
	margin-top: 10px;
	margin-bottom: 20px;
	text-align: center;
}
.card-page-history__list{
	.link{
		@include max-screen($xs){
			font-size: 13px;
		}  
	}
}
.learn-in{
	display: flex;
	justify-content: space-between;
	align-items: center;
	@include adaptive-value('margin-bottom', 100, 49, 1);
	@include adaptive-value('padding', 50, 40, 1);
	background-color: $gray-bg;

	
	@include max-screen($md){
		flex-direction: column;
		align-items: flex-start;
		padding-left: 20px;
		padding-right: 20px;

	}  
	.btn{
		flex-shrink: 0;
	}
}
.learn-in__title{
	max-width: 380px;
	margin-right: 15px;
	font-size: 28px;
	line-height: 34px;
	font-family: $bold;
	@include max-screen($md){
		margin-right: 0;
		margin-bottom: 30px;
		max-width: none;
	}
}
/*--------------------ba-slider CUSTOM----------------------*/
.page-history__content{
	.ba-slider{
		margin: 30px 0;
	}

}
.ba-slider .handle{
	width: 2px;
	background-color: $accent;

	&:hover, &:active{
		.ba-slider__arrow{
			.icon{
				opacity: 1;	
			}
		}
	}

	&:after{
		content: "";
		width: 10px;
		height: 40px;
		margin: 0;
		// left: 0;
		right: 0;
		transform: translateX(calc(50% - 1px));
		margin-top: -20px;
		background-color: $accent;
		border-radius: 0;
		border: none;
		box-shadow: none;
		line-height: normal;
	}
}
.ba-slider .handle.ba-draggable:after {
    width: 10px;
	height: 40px;
   	margin: 0;
   	margin-top: -20px;
    line-height: inherit;
    line-height: normal;
}
.ba-slider__arrows{
	@include absolute-center-old;
	display: flex;
	transform: translateX(calc(-50% - 3px));
	height: 24px;
	width: 48px;
	margin-left: -5px;
	margin-right: -5px;
}
.ba-slider__arrow{
	flex: 0 0 24px;
	position: relative;
	margin: 0 5px;

	
	.icon{
		@include absolute-center;
		font-size: 15px;
		color: #fff;
		opacity: 0.5;
		transition: opacity .3s;

		.ba-draggable &{
			opacity: 1;
		}
	}
}
.ba-slider__arrow--right{

}
/*--------------------END ba-slider CUSTOM----------------------*/ 

/*--------------------END history-page----------------------*/ 